.btn-initial {
  width: 45px;
  height: 45px;
  padding: 6px 0px;
}
.btn-initial:hover {
  box-shadow: 0 0 8px #0f2f44;
}

.text-blue-template {
  color: #3f6ad8;
}
.bg-blue-template {
  background-color: #3f6ad8;
}
@media screen and (max-width: 992px) {
  .full-height-mobile {
    min-height: calc(100vh - 120px);
  }
}

